@font-face {
    font-family: 'Brandon Grotesque';
    src: url('./BrandonGrotesque-Bold.woff2') format('woff2'),
        url('./BrandonGrotesque-Bold.woff') format('woff'),
        url('./BrandonGrotesque-Bold.ttf') format('truetype');
    font-weight: bold;
    font-style: normal;
} 

